import React, { Component } from "react";
import {
  CBadge,
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CSelect,
  CInput,
  CModal,
  CModalHeader,
  CModalTitle,
  CModalBody,
  CModalFooter,
  CFormGroup,
  CLabel,
  CInputFile,
} from "@coreui/react";
import { Link } from "react-router-dom";
import api from "../../api";
import authHeader from "../../auth-header";
import authHeaderFile from "../../auth-header-file";
import { MDBDataTableV5 } from "mdbreact";
import moment from "moment";
import swal from "sweetalert";
import { ScaleLoader } from "react-spinners";
import { css } from "@emotion/core";
const override = css`
  display: block;
  border-color: black;
  position: absolute;
  left: 43%;
  top: 50%;
  z-index: 1;
`;
var fromDate = null;
var toDate = null;
var fromDateVal = null;
var toDateVal = null;
var filterData = [];
class Appointments extends Component {
  constructor(props) {
    super(props);

    this.serviceStatus = this.serviceStatus.bind(this);
    this.serviceAction = this.serviceAction.bind(this);
    this.acceptApplication = this.acceptApplication.bind(this);

    this.filter = this.filter.bind(this);

    this.state = {
      data: [],
      loading: false,
      fromDateTypeFilter: "",
      toDateTypeFilter: "",
      statusTypeFilter: "",

      appointmentId: "null",
      filterType: "",
      showPaymentModal: false,
      selectedAppointment: null,

      amount: 0,
      amountError: "",
      payableAmount: 0,
      payableAmountError: "",
      serviceBreakdownFile: null,
      serviceBreakdownFileError: "",
    };
  }

  paymentStatus = (val, isPaymentInitiated) => {
    if (val === "unpaid") {
      if (isPaymentInitiated) {
        return (
          <CBadge className="mr-1" color="primary">
            INITIATED
          </CBadge>
        );
      }
      return (
        <CBadge className="mr-1" color="danger">
          UNPAID
        </CBadge>
      );
    } else if (val === "paid") {
      return (
        <CBadge className="mr-1" color="success">
          PAID
        </CBadge>
      );
    }
  };

  serviceStatus = (val, isEscalated, isResolved, escalationReason) => {
    if (val === "rejected") {
      return (
        <CBadge className="mr-1" color="danger">
          Rejected
        </CBadge>
      );
    } else if (val === "accepted") {
      if (isEscalated) {
        return (
          <>
            <CBadge className="mr-1" color="danger">
              ISSUE RAISED
            </CBadge>
            <br />
            <span style={{ fontWeight: "bold" }}>
              Reason:{" "}
              <span style={{ color: "red", fontWeight: "bolder" }}>
                {escalationReason}
              </span>
            </span>
          </>
        );
      }
      if (isResolved) {
        return (
          <CBadge className="mr-1" color="secondary">
            ISSUE RESOLVED
          </CBadge>
        );
      }

      return (
        <CBadge className="mr-1" color="success">
          Accepted
        </CBadge>
      );
    } else if (val === "pending") {
      return (
        <CBadge className="mr-1" color="primary">
          Pending
        </CBadge>
      );
    }
  };

  serviceAction = (id, token, status, payment_status, amount, data) => {
    console.log(id + token + status);
    return (
      <div style={{ width: "140px" }}>
        {status == "pending" && (
          <button
            className="btn cancel-btn"
            onClick={() => this.acceptApplication(id, token, "accepted")}
          >
            <i className="fa fa-check"></i>
          </button>
        )}

        {status == "pending" && (
          <button
            className="btn cancel-btn"
            onClick={() => this.acceptApplication(id, token, "rejected")}
          >
            <i className="fa fa-remove"></i>
          </button>
        )}

        {status == "accepted" &&
          payment_status == "unpaid" &&
          !data?.isPaymentInitiated &&
          !data?.isEscalated && (
            <button
              className="btn cancel-btn"
              onClick={() => {
                this.setState({
                  showPaymentModal: true,
                  selectedAppointment: { id, token },
                  amount,
                  amountError: "",
                  payableAmount: 0,
                  payableAmountError: "",
                  serviceBreakdownFile: null,
                  serviceBreakdownFileError: "",
                });
              }}
            >
              <i className="fa fa-money"></i>
            </button>
          )}

        {status == "accepted" &&
        payment_status == "unpaid" &&
        !data?.isPaymentInitiated &&
        data?.isEscalated ? (
          <button
            className="btn cancel-btn"
            onClick={() => {
              this.resolveEscalation(id);
            }}
          >
            RESOLVE
          </button>
        ) : null}

        <Link
          className="btn cancel-btn"
          to={{
            pathname: "/application-details",
            data: id, // your data array of objects
          }}
        >
          <i className="fa fa-info"></i>
        </Link>
      </div>
    );
  };

  acceptApplication = (id, token, status) => {
    var data = {
      id: id,
      status: status,
      token: token,
    };

    // this.setState({ loading: true });
    swal({
      title: "Are you sure?",
      text: "The request status will be changed!",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    }).then((willDelete) => {
      if (willDelete) {
        swal("Status updated successfully!", {
          icon: "success",
        });
        fetch(api.apiUrl + "update_application", {
          method: "POST",
          headers: authHeader(),
          body: JSON.stringify(data),
        })
          .then((response) => response.json())
          .then((responseJsonFromServerData) => {
            if (responseJsonFromServerData === "success") {
              this.componentDidMount();
            } else {
              this.props.history.push("/404");
            }
          })
          .catch((error) => {
            console.error(error);
            this.props.history.push("/404");
          });
      } else {
        // swal("Your imaginary file is safe!");
      }
    });
  };

  handleChange = (input) => (e) => {
    // console.log(input);
    // console.log(e.target.value);
    this.setState({ [input]: e.target.value });
    this.setState({ [input + "Error"]: "" });
  };

  initiatePaymentHandler = () => {
    let error = false;
    if (this.state.serviceBreakdownFile == null) {
      this.setState({
        serviceBreakdownFileError: "Select Service Breakdown Image",
      });
      error = true;
    } else {
      this.setState({ serviceBreakdownFileError: null });
    }

    if (this.state.payableAmount == null || this.state.payableAmount == "") {
      this.setState({ payableAmountError: "Enter Payable amount" });
      error = true;
    } else {
      if (isNaN(this.state.payableAmount)) {
        this.setState({ payableAmountError: "Not a valid number" });
        error = true;
      } else {
        this.setState({ payableAmountError: null });
      }
    }

    if (!error) {
      this.setState({ loading: true });

      var formData = new FormData();

      formData.append("serviceBreakdownFile", this.state.serviceBreakdownFile);
      formData.append("payableAmount", this.state.payableAmount);
      formData.append("id", this.state.selectedAppointment?.id ?? "");
      formData.append("token", this.state.selectedAppointment?.token ?? "");

      fetch(api.apiUrl + "initiate_appointment_payment", {
        method: "POST",
        headers: authHeaderFile(),
        body: formData,
      })
        .then((response) => response.text())
        .then((responseJsonFromServerData) => {
          console.log("responseJsonFromServerData", responseJsonFromServerData);
          if (responseJsonFromServerData === "success") {
            this.setState({ showPaymentModal: false });
            swal("Payment initiated successfully!", {
              icon: "success",
            });

            setTimeout(() => {
              this.componentDidMount();
            }, 2500);
          } else {
            this.props.history.push("/404");
          }
        })
        .catch((error) => {
          console.error(error);
          this.props.history.push("/404");
        });
    }
  };

  resolveEscalation = (id) => {
    this.setState({ loading: true });

    fetch(api.apiUrl + "resolveEscalatedAppointment", {
      method: "POST",
      headers: authHeader(),
      body: JSON.stringify({ appointmentId: id }),
    })
      .then((response) => response.text())
      .then((responseJsonFromServerData) => {
        console.log("responseJsonFromServerData", responseJsonFromServerData);
        if (responseJsonFromServerData === "success") {
          this.setState({ showPaymentModal: false });
          swal("Issue resolved successfully!", {
            icon: "success",
          });

          setTimeout(() => {
            this.componentDidMount();
          }, 2500);
        } else {
          this.props.history.push("/404");
        }
      })
      .catch((error) => {
        console.error(error);
        this.props.history.push("/404");
      });
  };

  onChangeFile = (event) => {
    const imageFile = event.target.files[0];
    var stat = false;
    if (!imageFile) {
      console.log("1");
      this.setState({
        serviceBreakdownFileError: "Please select file",
        serviceBreakdownFile: null,
      });
      stat = true;
      //   return false;
    }
    // console.log(imageFile.name);
    if (!imageFile.name.match(/\.(jpg|jpeg|png)$/)) {
      console.log("2");
      this.setState({
        serviceBreakdownFileError:
          "Please select valid file. Allowed types (jpg | jpeg | png)",
        serviceBreakdownFile: null,
      });
      stat = true;
      //   return false;
    }

    if (Math.round(imageFile.size / 1024) > 2048) {
      console.log("3");
      this.setState({
        serviceBreakdownFileError: "Max file size 2MB",
        serviceBreakdownFile: null,
      });
      stat = true;
      //   return false;
    }

    if (stat === false) {
      this.setState({
        serviceBreakdownFile: event.target.files[0],
        serviceBreakdownFileError: "",
      });
    } else {
      console.log("failed");
      this.setState({
        serviceBreakdownFile: null,
      });
    }
  };

  filter = (val, type) => {
    var info = [];

    this.setState({
      appointmentId: val,
      filterType: type,
    });

    if (type === "reset") {
      filterData = {};
      fromDate = null;
      toDate = null;
      fromDateVal = null;
      toDateVal = null;
      this.setState({
        statusTypeFilter: "",
        fromDateTypeFilter: "",
        toDateTypeFilter: "",
      });
    }

    if (type === "from") {
      this.setState({ fromDateTypeFilter: val });
      //   localStorage.setItem("fromDate", val);
      fromDate = new Date(val);
      fromDateVal = val;
    }

    if (type === "to") {
      this.setState({ toDateTypeFilter: val });
      //   localStorage.setItem("toDate", val);
      toDate = new Date(val);
      toDateVal = val;
    }

    if (fromDate != null && toDate != null) {
      if (fromDate.getTime() >= toDate.getTime()) {
        swal("From Date should be greater than To Date");
      } else if (fromDate.getTime() < toDate.getTime()) {
        filterData = {
          fromDate: fromDateVal,
          toDate: toDateVal,
          status:
            this.state.statusTypeFilter !== ""
              ? this.state.statusTypeFilter
              : "null",
        };
      }
    }

    if (type === "status") {
      this.setState({ statusTypeFilter: val });
      filterData = {
        fromDate: fromDate !== "" ? fromDateVal : "null",
        toDate: toDate !== "" ? toDateVal : "null",
        status: val,
      };
    }

    this.setState({ loading: true });
    fetch(api.apiUrl + "get_applications", {
      method: "POST",
      headers: authHeader(),
      body: JSON.stringify(filterData),
    })
      .then((response) => response.json())
      .then((responseJsonFromServerData) => {
        responseJsonFromServerData.map((item, key) => {
          var arr = {
            client: item.user,
            shop: item.shop,
            date: moment(item.appointmentDate).format("MMM Do YYYY"),
            time: moment(item.appointmentTime, "hh:mm:ss").format("hh:mm A"),
            status: this.serviceStatus(
              item.isApproved,
              item.isEscalated,
              item.isResolved,
              item?.escalationReason
            ),
            payment_status: this.paymentStatus(
              item?.paid,
              item?.isPaymentInitiated
            ),
            action: this.serviceAction(
              item.id,
              item.firebaseToken,
              item.isApproved,
              item?.paid,
              item?.amount ?? 0,
              item
            ),
          };
          info.push(arr);
        });
        this.setState({
          data: info,
          loading: false,
        });
      })
      .catch((error) => {
        console.error(error);
        this.setState({ loading: false });
        this.props.history.push("/404");
      });
  };

  componentDidMount() {
    var info1 = [];
    this.setState({ loading: true });
    fetch(api.apiUrl + "get_applications", {
      method: "POST",
      headers: authHeader(),
      // body: JSON.stringify(profile_data),
    })
      .then((response) => response.json())
      .then((responseJsonFromServerData) => {
        // if (responseJsonFromServerData.length !== 0) {
        responseJsonFromServerData.map((item, key) => {
          var arr = {
            client: item.user,
            shop: item.shop,
            date: moment(item.appointmentDate).format("MMM Do YYYY"),
            time: moment(item.appointmentTime, "hh:mm:ss").format("hh:mm A"),
            status: this.serviceStatus(
              item.isApproved,
              item.isEscalated,
              item.isResolved,
              item?.escalationReason
            ),
            payment_status: this.paymentStatus(
              item?.paid,
              item?.isPaymentInitiated
            ),
            action: this.serviceAction(
              item.id,
              item.firebaseToken,
              item.isApproved,
              item?.paid,
              item?.amount ?? 0,
              item
            ),
          };
          info1.push(arr);
        });
        this.setState({
          data: info1,
          loading: false,
        });
        // } else {
        //   //redirect to 404 page
        //   this.props.history.push("/404");
        // }
      })
      .catch((error) => {
        console.error(error);
        this.setState({ loading: false });
      });
  }

  render() {
    var datatable = {
      columns: [
        {
          label: "Client",
          field: "client",
          sort: "disabled",
          width: 150,
          attributes: {
            "aria-controls": "DataTable",
            "aria-label": "Icon",
          },
        },
        {
          label: "Shop Name",
          field: "shop",
          sort: "disabled",
          width: 270,
        },
        {
          label: "Appointment Date",
          field: "date",
          sort: "disabled",
          width: 200,
        },
        {
          label: "Appointment Time",
          field: "time",
          sort: "disabled",
          width: 100,
        },
        {
          label: "Status",
          field: "status",
          sort: "disabled",
          width: 150,
        },
        {
          label: "Payment Status",
          field: "payment_status",
          sort: "disabled",
          width: 150,
        },
        {
          label: "Action",
          field: "action",
          sort: "disabled",
          width: 270,
        },
      ],
      rows: [...this.state.data],
    };
    return (
      <>
        <CRow>
          <CCol>
            <CCard>
              <ScaleLoader
                height={40}
                width={12}
                css={override}
                size={150}
                //size={"150px"} this also works
                color={"#B54545"}
                loading={this.state.loading}
              />
              <CCardHeader>
                <CRow>
                  <CCol sm="6" xs="8" md="8">
                    <h3>Bookings</h3>
                  </CCol>
                  <CCol sm="6" xs="4" md="4"></CCol>
                </CRow>
              </CCardHeader>
              <CCardBody>
                <CRow>
                  <CCol md="3" sm="3" xs="12">
                    <CInput
                      autoFocus
                      type="date"
                      onChange={(e) => this.filter(e.target.value, "from")}
                      value={this.state.fromDateTypeFilter}
                      style={{ margin: "5px" }}
                    ></CInput>
                  </CCol>

                  <CCol md="3" sm="3" xs="12">
                    <CInput
                      type="date"
                      onChange={(e) => this.filter(e.target.value, "to")}
                      value={this.state.toDateTypeFilter}
                      style={{ margin: "5px" }}
                    ></CInput>
                  </CCol>

                  <CCol md="3" sm="3" xs="12">
                    <CSelect
                      custom
                      onChange={(e) => this.filter(e.target.value, "status")}
                      value={this.state.statusTypeFilter}
                      style={{ margin: "5px" }}
                    >
                      <option value="null">Select Status</option>
                      <option value="pending">Pending</option>
                      <option value="accepted">Accepted</option>
                      <option value="rejected">Rejected</option>
                    </CSelect>
                  </CCol>

                  <CCol md="3" sm="3" xs="12">
                    <CButton
                      onClick={(e) => this.filter(e.target.value, "reset")}
                      style={{ margin: "5px" }}
                      className="btn submit-btn"
                    >
                      Reset
                    </CButton>
                  </CCol>
                </CRow>
                <CRow>
                  <CCol md="12" sm="12" xs="12">
                    <MDBDataTableV5
                      hover
                      entriesOptions={[6, 10]}
                      entries={6}
                      pagesAmount={4}
                      data={datatable}
                      pagingTop
                      searchTop
                      searchBottom={false}
                      barReverse
                      responsive
                    />
                  </CCol>
                </CRow>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>

        <CModal
          centered
          show={this.state.showPaymentModal}
          onClose={() => this.setState({ showPaymentModal: false })}
        >
          <CModalHeader>
            <CModalTitle>Initiate Payment</CModalTitle>
          </CModalHeader>
          <CModalBody>
            <form id="initiatePaymentForm">
              <CFormGroup>
                <CLabel htmlFor="amount">Approx Amount</CLabel>

                <CInput
                  autoFocus
                  type="text"
                  placeholder="Enter Approx Amount"
                  onChange={this.handleChange("amount")}
                  style={{
                    borderColor: this.state.amountError === "" ? "" : "red",
                  }}
                  value={this.state.amount}
                  disabled
                />
              </CFormGroup>
              <p
                className="error"
                style={{
                  display: this.state.amountError === "" ? "none" : "block",
                }}
              >
                <i className="fa fa-exclamation-triangle"></i>{" "}
                {this.state.amountError}
              </p>

              <br />

              <CFormGroup>
                <CLabel htmlFor="payableAmount">Payable Amount</CLabel>

                <CInput
                  autoFocus
                  type="text"
                  placeholder="Enter Payable Amount"
                  onChange={this.handleChange("payableAmount")}
                  style={{
                    borderColor:
                      this.state.payableAmountError === "" ? "" : "red",
                  }}
                />
              </CFormGroup>
              <p
                className="error"
                style={{
                  display:
                    this.state.payableAmountError === "" ? "none" : "block",
                }}
              >
                <i className="fa fa-exclamation-triangle"></i>{" "}
                {this.state.payableAmountError}
              </p>

              <br />

              <CFormGroup>
                <CLabel htmlFor="">Service breakdown file</CLabel>
                <CInputFile id="" name="" onChange={this.onChangeFile} />
              </CFormGroup>

              <p
                className="error"
                style={{
                  display:
                    this.state.serviceBreakdownFileError === ""
                      ? "none"
                      : "block",
                }}
              >
                <i className="fa fa-exclamation-triangle"></i>{" "}
                {this.state.serviceBreakdownFileError}
              </p>
            </form>
          </CModalBody>
          <CModalFooter>
            <CButton
              color="secondary"
              onClick={() => this.setState({ showPaymentModal: false })}
            >
              Close
            </CButton>
            <CButton
              color="primary"
              onClick={() => {
                this.initiatePaymentHandler();
              }}
            >
              Send Payment Request
            </CButton>
          </CModalFooter>
        </CModal>
      </>
    );
  }
}
export default Appointments;
